*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 5em;
}

input{
  width: 33%;
  font-size: 12px;
  padding: 5px;
  color: gray;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 180px;
}